<script>
import { mapState, mapMutations } from 'vuex';
import { handleClickEvent } from '../util/util.js';
import { setInterval, clearInterval } from 'timers';
export default {
    props:{
      noMask:{
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        isLogWhy: true,
        maskBtn: false,
        // 控制project子页面下，导航里project项处于active 状态【】
        subProject:false,
        isClickLog: false,
        navShow: true
      }
    },
    computed: {
      ...mapState({
        navItemstatus: state => state.navItemstatus,
        isMask: state => state.isMask
      })
    },
    mounted() {
      this.$nextTick(() => {
        const stickyEl = document.querySelector('.header');
          const u = navigator.userAgent;
          const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; 
          const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
          const fn = function() {
            const scrollT = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
              if (scrollT > 0) {
                  stickyEl.classList.add('fixed-top');
              }else {
                  stickyEl.classList.remove('fixed-top');
              }
          }
          if(isAndroid) {
            this.SET_BANNER_ISANDROID(true)
            window.addEventListener('scroll', fn)
            // window.onscroll = function() {
            //   const scrollT = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
            //   console.log(scrollT)
            //   if (scrollT > 0) {
            //       stickyEl.classList.add('fixed-top');
            //   }else {
            //       stickyEl.classList.remove('fixed-top');
            //   }
            // };
          }else if(isiOS) {
            this.SET_BANNER_ISIOS(true)
            stickyEl.classList.add('sticky');
          }else {
            if (!("classList" in document.documentElement)) {
              Object.defineProperty(window.Element.prototype, 'classList', {
                get: function () {
                  var self = this
                  function update(fn) {
                    return function () {
                      var className = self.className.replace(/^\s+|\s+$/g, ''),
                        valArr = arguments
                      return fn(className, valArr)
                    }
                  }
                  function add_rmv (className, valArr, tag) {
                    for (var i in valArr) {
                      if(typeof valArr[i] !== 'string' || !!~valArr[i].search(/\s+/g)) throw TypeError('the type of value is error')
                      var temp = valArr[i]
                      var flag = !!~className.search(new RegExp('(\\s+)?'+temp+'(\\s+)?'))
                      if (tag === 1) {
                        !flag ? className += ' ' + temp : ''
                      } else if (tag === 2) {
                        flag ? className = className.replace(new RegExp('(\\s+)?'+temp),'') : ''
                      }
                    }
                    self.className = className
                    return tag
                  }

                  return {
                    add: update(function (className, valArr) {
                      add_rmv(className, valArr, 1)
                    }),
                    remove: update(function (className, valArr) {
                      add_rmv(className, valArr, 2)
                    }),
                    toggle: function (value) {
                      if(typeof value !== 'string' || arguments.length === 0) throw TypeError("Failed to execute 'toggle' on 'DOMTokenList': 1 argument(string) required, but only 0 present.")
                      if (arguments.length === 1) {
                        this.contains(value) ? this.remove(value) : this.add(value)
                        return
                      }
                      !arguments[1] ? this.remove(value) : this.add(value)
                    }
                  }
                }
              })
            }
            stickyEl.classList.add('fixed-top');
          }
        if(this.$i18n.locale === 'zh') {
          this.isLogWhy = true
          this.$refs.i18nBgc.classList.remove('icon-navbar_icon_translate_chinese_press')
            this.$refs.i18nBgc.classList.add('icon-navbar_icon_translate_english_normal')
          return;
        }
        if(this.$i18n.locale === 'en') {
          this.isLogWhy = false
          this.$refs.i18nBgc.classList.remove('icon-navbar_icon_translate_english_normal')
            this.$refs.i18nBgc.classList.add('icon-navbar_icon_translate_chinese_press')
          return;
        }
      });
      //  判断当前路由对象是project子页面，则导航project项处于active状态【】
      switch(this.$route.name){
        case "projectTpg":
        this.subProject = true
        break
         case "projectTse":
        this.subProject = true
        break
         case "projectLiYing":
        this.subProject = true
        break
         case "projectAudioAndVideo":
        this.subProject = true
        break
      }
    },
    beforeDestroy() {
      const fn = function() {
        const scrollT = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
          // console.log(scrollT)
          if (scrollT > 0) {
              stickyEl.classList.add('fixed-top');
          }else {
              stickyEl.classList.remove('fixed-top');
          }
      }
      window.removeEventListener('scroll', fn)
      this.navShow = false
    },
    methods: {
      ...mapMutations(['SET_NAVITEM_STATUS', 'SET_NAV_MASK', 'SET_BANNER_ISANDROID', 'SET_BANNER_ISIOS']),
      // 埋点
      handleClickEvent(id, eventName, params) {
        handleClickEvent(id, eventName, params)
        const warpLink = [
          {id0:'87510', id:'82093', eventName: 'click_solution_TRTC'},
          {id0:'87508', id:'82094', eventName: 'click_solution_ILVB'},
          {id0:'87507', id:'82095', eventName: 'click_solution_GME'}
        ]
        warpLink.forEach((item) => {
          if(id === item.id0) {
            handleClickEvent(item.id, item.eventName)
          }
        })
      },
      // log点击跳转首页，且关闭遮罩层
      handleClickCloseMask() {
        this.isClickLog = true;
        const times = setInterval(() => {
          this.isClickLog = false;
          clearInterval(times)
        }, 200)
        if(this.$router.history.current.path === '/index') {
          window.scrollTo(0,0)
        }else {
          this.SET_NAV_MASK(false);
          this.$router.push({
            path: '/index'
          })
        }
      },
      // i18n转换
      changeLanguge(val, flag){
        if (!this.noMask) {
          return
        }
        if(flag === 0) {
          if(this.$i18n.locale === val) {
            return;
          }
          if(this.$i18n.locale === 'zh') {
            this.$i18n.locale="en"
            this.isLogWhy = false
            return;
          }
          if(this.$i18n.locale === 'en') {
            this.$i18n.locale="zh"
            this.isLogWhy = true
            return;
          }
        } else {
          if(this.$i18n.locale=="zh"){
            this.$i18n.locale="en"
            this.isLogWhy = false
            this.$refs.i18nBgc.classList.remove('icon-navbar_icon_translate_english_normal')
            this.$refs.i18nBgc.classList.add('icon-navbar_icon_translate_chinese_press')
          }else{
            this.$i18n.locale="zh"
            this.isLogWhy = true
            this.$refs.i18nBgc.classList.remove('icon-navbar_icon_translate_chinese_press')
            this.$refs.i18nBgc.classList.add('icon-navbar_icon_translate_english_normal')
          }
        }
      },
      
      // 点击弹出遮罩层
      handleClickMaskBtn() {
        if (!this.noMask) {
          return
        }
        this.SET_NAV_MASK(!this.isMask)
      },
      // 鼠标移动至当前nav展示相应的内容
      handleMosemove(val) {
        const arr = [1, 2, 3, 4];
        const idx = arr[(val-1)];
        this.SET_NAVITEM_STATUS({ index: idx, item: true })
      },
      // 鼠标移动至当前nav之外展示相应的内容
      handleMouseleave(val) {
        const arr = [1, 2, 3, 4];
        const idx = arr[(val-1)];
        this.SET_NAVITEM_STATUS({ index: idx, item: false });
      }
    }
  }
</script>

