<template>
  <div class="mask hidden-md hidden-lg" v-if="isMask">
    <ul>
      <li 
      v-for="(item, index) in newnavList" 
      :key="item.name"
      >
        <p ref="blueFont" 
        @touchstart="handleTouchStart(item, index)"
        @touchend="handleTouchEnd(item, index)"
        @click="handleClickOpen(item, index)">{{item.name}}</p>
        <ul class="childenList">
          <li
          @click="handleClickRouter(items.link, items.index);handleClickEvent(items.id, items.eventName)"
          v-for="(items) in item.childen"
          :key="items.name"
          v-show="item.showList">
            <p>
              {{ items.name }}
            </p>
            <span 
              v-if="items.bgc"
              class="icon icon-menu_icon_share_default"></span>
          </li>
        </ul>
        <span 
        ref="upDown"
        :class="{'upDown':true, 'icon':true, 'icon-function_icon_1_normal':!item.showList,'icon-function_icon_2_normal':item.showList}"
        v-show="item.childen && item.childen.length>0"
        ></span>
      </li>
    </ul>
  </div>
</template>

<script>
import { handleClickEvent } from '../util/util.js';
import { mapState, mapMutations } from 'vuex';


export default {
    data() {
      return {
        newnavList:[]
      }
    },
    computed: {
      ...mapState({
        isMask: state => state.isMask
      }),
      navList: function(){
        return this.$t('homeLanguge.navList')
      }
    },
    watch: {
      navList:function() {
        this.newnavList = []
        this.newnavList = this.navList
      }
    },
    mounted() {
      this.newnavList = this.navList
    },
    beforeDestroy() {
      this.SET_NAV_MASK(false);
    },
    methods: {
      ...mapMutations(['SET_NAV_MASK', 'SET_SCROLLINDECX']),
      handleClickRouter(val, index) {
        const idx = index || 0;
        this.SET_SCROLLINDECX(idx);
        this.SET_NAV_MASK(false);
        if(val.indexOf('http')>-1) {
          window.location.href = val
        } else {
            this.$router.push({
              path: val,
          })
        }
      },
      handleTouchStart(item, index) {
        this.$refs.blueFont[index].style.background = 'rgba(34,34,34,1)';
      },
      handleTouchEnd(item, index) {
        this.$refs.blueFont[index].style.background = 'rgba(24,24,24,1)';
      },
      handleClickOpen(item, index) {
        if(index == 0) {
          this.SET_NAV_MASK(false)
          this.$router.push({
            path: '/index'
          })
        }
        item.showList = !item.showList;
        if(item.showList) {
          this.$refs.blueFont[index].style.color = '#3282FF';
        } else {
          this.$refs.blueFont[index].style.color = '#fff';
        }
      },
      // 埋点
    handleClickEvent(id, eventName, params) {
      handleClickEvent(id, eventName, params)
      }
    }
}
</script>

